import React, { useState } from "react";
import lc from "./messages.module.css";

function Sms(props) {
  return (
    <div>
      <span className={lc.sms}>{props.message}</span>
    </div>
  );
}

function Messages(props) {
  let [textValue, setTextValue] = useState("");

  let onSendSms = (evt) => {
    if (textValue != "") {
      props.sendSms(textValue);
      setTextValue("");
    }
    evt.preventDefault();
  };

  let messagesElements = props.messagesData.map((m) => {
    return <Sms message={m.message} key={m.id} />;
  });

  return (
    <div className={lc.messages}>
      <div className={lc.smsBlock}>{messagesElements}</div>
      <form className={lc.input_block} onSubmit={onSendSms}>
        <input
          value={textValue}
          onChange={(e) => setTextValue(e.target.value)}
          placeholder="Напишите сообщение..."
          autoComplete="off"
        />
        <button>Send</button>
      </form>
    </div>
  );
}

export default Messages;
